import React from 'react'
import CenterBoxContainer from '../../../../generics/center_box_container'
import Color from '../../../../../constants/color'
import Box from '../../../../generics/box'

const ConceituacaoParteUm = () => (
    <div>
        <h2>Conceituação</h2>
        
        <p>Os problemas do mundo real s&atilde;o, por natureza, interdisciplinares, ou seja, requerem o conhecimento de temas tratados em duas ou mais disciplinas espec&iacute;ficas nos curr&iacute;culos escolares para que se elabore uma solu&ccedil;&atilde;o.</p>

        <p>De forma an&aacute;loga, os temas de pesquisa de diferentes &aacute;reas do conhecimento, requerem o uso do conhecimento de mais de uma &aacute;rea para a elabora&ccedil;&atilde;o de novas teorias sobre a realidade, nos diversos campos do conhecimento humano.</p>

        <p>Em ambos os casos, h&aacute; raz&otilde;es para a fragmenta&ccedil;&atilde;o em disciplinas, buscando uma abordagem que levasse a um conhecimento mais profundo sobre os mais intrincados ou at&eacute; mesmo complexos problemas que povoam nossas vidas, tanto no campo pr&aacute;tico quanto no campo te&oacute;rico. Entretanto, com o passar dos tempos, pesquisadores e educadores se deram conta das limita&ccedil;&otilde;es introduzidas pela abordagem disciplinar.</p>

        <p>J&aacute; Descartes, considerado um dos pais da ci&ecirc;ncia moderna, algumas vezes criticado pela proposta reducionista de sua metodologia cient&iacute;fica, j&aacute; chamava aten&ccedil;&atilde;o para a necessidade de uma integra&ccedil;&atilde;o entre os diversos campos do saber (Pombo, 1994).</p>

        <p>A crescente fragmenta&ccedil;&atilde;o dos campos de estudo tem dificultado a elabora&ccedil;&atilde;o de propostas integradoras para o conhecimento ao mesmo tempo que implica na cria&ccedil;&atilde;o de novas disciplinas no contexto escolar. Estes fatos, t&ecirc;m motivado os educadores a buscarem propostas pedag&oacute;gicas interdisciplinares (Pombo, 1994).</p>

        <p> Como dissemos anteriormente, muitos problemas pr&aacute;ticos, do nosso cotidiano e mesmo alguns mais complexos precisam do aporte de conhecimentos de diferentes &aacute;reas do saber. Podemos citar alguns:</p>
        
        <ul>
            <li>Planejar uma viagem de f&eacute;rias considerando a visita a cidades com diferentes caracter&iacute;sticas geogr&aacute;ficas, art&iacute;sticas, culin&aacute;rias etc. Considerando os limites de tempo, disponibilidade de datas e, de recursos financeiros dispon&iacute;veis.</li>
            <li>Produzir um curta metragem contando a hist&oacute;ria de um personagem hist&oacute;rico de seu estado, cidade ou bairro.</li>
            <li>Organizar um almo&ccedil;o para comemora&ccedil;&atilde;o dos 50 anos de sua escola, a festa de formatura ou mesmo a festa de debutantes.</li>
            <li>Organizar uma campanha de conscientiza&ccedil;&atilde;o sobre a produ&ccedil;&atilde;o de lixo dom&eacute;stico.</li>
            <li>Organizar uma campanha de esclarecimento sobre cuidados com a compra de alimentos.</li>
        </ul>

        <CenterBoxContainer>
            <h3>QUADRO 1: CONCEITUANDO INTERDISCIPLINARIDADE</h3>
            <Box backgroundColor={Color.BLUE_BOX}>
                <p>Do ponto de vista pedag&oacute;gico, a interdisciplinaridade pode ser entendida como  &ldquo;a intera&ccedil;&atilde;o com um ou mais objetos de estudo segundo a perspectiva de diferentes disciplinas buscando a elabora&ccedil;&atilde;o de solu&ccedil;&otilde;es para problemas concretos que possibilite economia de esfor&ccedil;os e at&eacute; mesmo uma melhor gest&atilde;o de recursos, com respeito ao controle de repeti&ccedil;&otilde;es cansativas, &agrave; an&aacute;lise de dados, a utiliza&ccedil;&atilde;o de equipamentos ou &agrave; produ&ccedil;&atilde;o de dados.&rdquo; (Pombo, 1994)</p>
            </Box>
        </CenterBoxContainer>
    </div>
)

export default ConceituacaoParteUm