import React from 'react'
import CenterBoxContainer from '../../../generics/center_box_container'
import Box from '../../../generics/box'
import Color from '../../../../constants/color'

const Conceituacao = () => (
    <div>
        <h2>Conceituação</h2>
        <p>Ao longo de nossas vidas buscamos compreender o mundo em que vivemos, os seres que nele habitam, principalmente os de nossa esp&eacute;cie, nosso sistema solar, as gal&aacute;xias, enfim o universo. Al&eacute;m das preocupa&ccedil;&otilde;es mais gerais, metaf&iacute;sicas, precisamos compreender o mundo por quest&otilde;es pr&aacute;ticas, para nossa sobreviv&ecirc;ncia. Do princ&iacute;pio ao fim da vida estamos buscando identificar regularidades.</p>
        <p>Ao mudarmos para uma nova cidade, precisamos compreend&ecirc;-la para podermos andar com mais efic&aacute;cia por suas ruas, sabermos onde comprar alimentos, onde encontrar um m&eacute;dico ou at&eacute; mesmo onde assistir um show musical. Na feira, olhamos para as frutas, as verduras e os legumes, buscando identificar a qualidade, o estado de maturidade, a origem, etc.</p>
        <p>Quando dirigimos, precisamos entender o fluxo do tr&acirc;nsito, a arquitetura das vias, por onde fica mais f&aacute;cil circular, em quais trechos e hor&aacute;rios o tr&acirc;nsito fica engarrafado.</p>
        <p>Em um momento estamos evoluindo nossa compreens&atilde;o do mundo, construindo padr&otilde;es atrav&eacute;s da observa&ccedil;&atilde;o e da reflex&atilde;o, em outro, estamos reconhecendo padr&otilde;es j&aacute; identificados.</p>
        <p>Um exemplo bem t&iacute;pico &eacute; encontrar um determinado local que desejamos visitar. Pegamos o &ocirc;nibus e, ao percebermos a proximidade de nosso destino, solicitamos ao condutor que pare no pr&oacute;ximo ponto. Nas primeiras vezes, a dificuldade &eacute; grande, ainda n&atilde;o conseguimos reconhecer quando estamos nos aproximando, os detalhes das casas, as &aacute;rvores, os an&uacute;ncios, s&atilde;o muitas novidades, ent&atilde;o frequentemente nos confundimos e erramos. Ap&oacute;s algumas repeti&ccedil;&otilde;es, o reconhecimento do trajeto e seus detalhes torna-se cada vez mais f&aacute;cil.</p>
        <p>Se pedirmos para um grupo de pessoas que n&atilde;o conhecemos pessoalmente e cujas datas de nascimento tamb&eacute;m desconhecemos que listem filmes, m&uacute;sicas, modas, tipos de lazeres e acontecimentos que marcaram suas adolesc&ecirc;ncias, &eacute; bem prov&aacute;vel que consigamos identificar a d&eacute;cada em que cada uma delas viveu esse per&iacute;odo de suas vidas. Isso porque filmes, m&uacute;sicas, modas, tipos de lazeres e acontecimentos marcantes costumam estabelecer um padr&atilde;o que permite identificar uma &eacute;poca.</p>
        <p>Desde crian&ccedil;a, o h&aacute;bito de identificar padr&otilde;es nos acompanha, &eacute; uma constru&ccedil;&atilde;o continuada e o nosso repert&oacute;rio de padr&otilde;es n&atilde;o p&aacute;ra de crescer e de se reconstruir. Somos uma competent&iacute;ssima f&aacute;brica de padr&otilde;es. Podemos dizer que ao longo da vida n&atilde;o paramos de construir um variad&iacute;ssimo repert&oacute;rio de padr&otilde;es, especializando-os quando necess&aacute;rio e sempre buscando generaliz&aacute;-los para ampliar a gama de situa&ccedil;&otilde;es onde possam ser usados. Ao nos deparamos com um novo problema, &eacute; muito comum adotarmos um padr&atilde;o de comportamento para abord&aacute;-lo.</p>
        <CenterBoxContainer>
            <p><strong>&quot;J&aacute; vi alguma situa&ccedil;&atilde;o parecida?&quot;.</strong></p>
        </CenterBoxContainer>
        <p>Padr&otilde;es de cores, de formas, de sons, de movimentos, de comportamentos, de consist&ecirc;ncia, de uso de palavras, padr&otilde;es de sequ&ecirc;ncias, padr&otilde;es recursivos e muitos outros.</p>
        <p>A seguir, apresentamos uma listagem de situa&ccedil;&otilde;es em que as solu&ccedil;&otilde;es requerem o reconhecimento de padr&otilde;es:</p>
        <ul>
            <li>Prever o pr&oacute;ximo n&uacute;mero em uma dada sequ&ecirc;ncia de n&uacute;meros; </li>
            <li>Identificar uma esp&eacute;cie de p&aacute;ssaro pelo seu padr&atilde;o de voo;</li>
            <li>Perceber o que existe de comum em piadas engra&ccedil;adas e criar uma nova;</li>
            <li>Estimar a hora a partir da posi&ccedil;&atilde;o do sol;</li>
            <li>Antecipar uma poss&iacute;vel chegada de chuva a partir da configura&ccedil;&atilde;o das nuvens;</li>
            <li>Identificar o sentido do vento, olhando para uma biruta;</li>
            <li>Escolher uma fruta pela cor de sua casca;</li>
            <li>Diagnosticar uma doen&ccedil;a com base em sintomas, apar&ecirc;ncias e comportamentos;</li>
            <li>Perceber a chegada de uma pessoa conhecida pelo ritmo do som de sua pisada;</li>
            <li>Montar um quebra-cabe&ccedil;a;</li>
            <li>Identificar uma m&uacute;sica pelo padr&atilde;o de notas m&uacute;sicas de seu in&iacute;cio (popularmente conhecido por jogo das sete notas);</li>
            <li>Antecipar o que vai acontecer em um filme ou novela, baseado em eventos anteriores.</li>
        </ul>
        <CenterBoxContainer>
            <h3>Quadro 4.1: Conceituando Reconhecimento de Padr&otilde;es</h3>
            <Box backgroundColor={Color.BLUE_BOX}>
                <p>Entende-se por <strong>reconhecimento de padr&otilde;es</strong> a associa&ccedil;&atilde;o de algum objeto (ou parte dele), tang&iacute;vel ou conceitual, com padr&otilde;es familiares que permitam identific&aacute;-lo e classific&aacute;-lo.</p>            </Box>
        </CenterBoxContainer>
        <p>O reconhecimento de um determinado objeto nos permite trat&aacute;-lo segundo as prescri&ccedil;&otilde;es pr&eacute;-elaboradas, constru&iacute;das a partir de experi&ecirc;ncias anteriores.</p>
    </div>
)

export default Conceituacao