import React from 'react'
import ReactPaginate from 'react-paginate'
import LocalStorageUtils from '../../../utils/localStorageUtils.js'
import './styles.css'

class Pagination extends React.Component {
    constructor(props){
        super(props)

        let page = parseInt(LocalStorageUtils.getOpenPage())

        this.state = {
            availablePages: this.props.availablePages,
            page: page,
            marginPagesDisplayed: 1,
            pageRangeDisplayed: 0
        }
    }

    onPageChange = data => {
        let selectedPage = data.selected + 1
        LocalStorageUtils.setOpenPage(selectedPage)

        this.props.onPageChange()
    }

    render() {
        return (
            <ReactPaginate
                previousLabel={'<'}
                nextLabel={'>'}
                breakLabel={'...'}
                pageCount={this.state.availablePages}
                initialPage={this.state.page - 1}
                activeClassName={'selected-page'}
                marginPagesDisplayed={this.marginPagesDisplayed}
                pageRangeDisplayed={this.pageRangeDisplayed}
                onPageChange={this.onPageChange}
                containerClassName={'pagination'}
            />
        )
    }
}

export default Pagination

