import React from 'react'
import CenterBoxContainer from '../../../generics/center_box_container'
import Color from '../../../../constants/color'
import Box from '../../../generics/box'

const Conceituacao = () => (
    <div>
        <h3>Conceituação</h3>
        <p>A coopera&ccedil;&atilde;o tem sido um dos principais recursos adotados ao longo do tempo para lidar com desafios da solu&ccedil;&atilde;o de problemas em geral e, particular, em processos de aprendizagem. Acreditamos que o Pensamento Computacional, atrav&eacute;s dos pilares discutidos neste texto, pode auxiliar bastante nesse desafio, por exemplo atrav&eacute;s da sistematiza&ccedil;&atilde;o de um conjunto de estrat&eacute;gias e instrumentos para a an&aacute;lise de problemas e da representa&ccedil;&atilde;o organizada de solu&ccedil;&otilde;es, ampliando o conjunto inicial. Nosso objetivo no restante deste texto &eacute; ilustrar situa&ccedil;&otilde;es simples onde a coopera&ccedil;&atilde;o &eacute; adequada ou necess&aacute;ria, discutindo como os pilares do Pensamento Computacional podem facilitar a coopera&ccedil;&atilde;o entre os integrantes de um grupo, tornando expl&iacute;citos v&aacute;rios dos elementos envolvidos de modo a potencializar a aprendizagem.</p>
        <p>Mas cooperar vale a pena? Albert Einstein disse que &quot;Nada realmente valioso pode ser alcan&ccedil;ado exceto pela coopera&ccedil;&atilde;o altru&iacute;sta de muitos indiv&iacute;duos&quot;. Sobre o tema, (JHONSON E JHONSON, 1989) baseados em 375 relatos de pesquisa, afirmam que muitas pesquisas indicam que a coopera&ccedil;&atilde;o, quando comparada com a&ccedil;&atilde;o individual ou competitiva, resulta em: (a) mais conquistas e maior produtividade; (b) relacionamentos mais cuidadosos, solid&aacute;rios, e comprometidos; e (c) maior sa&uacute;de psicol&oacute;gica, compet&ecirc;ncia social e auto-estima. </p>
        <p>Al&eacute;m dessas categorias gerais, podemos incluir benef&iacute;cios mais diretamente relacionados &agrave; solu&ccedil;&atilde;o de problemas, como a negocia&ccedil;&atilde;o de significados, a divis&atilde;o de tarefas, combina&ccedil;&atilde;o e recombina&ccedil;&atilde;o de solu&ccedil;&otilde;es, etc. Independente de muitas outras vantagens potenciais, a coopera&ccedil;&atilde;o tem sido aceita como uma pr&aacute;tica ben&eacute;fica especialmente importante ao lidarmos com problemas complexos, embora n&atilde;o restrita a eles.</p>
        <CenterBoxContainer>
            <h3>Quadro 1: Conceituando Cooperação</h3>
            <Box backgroundColor={Color.BLUE_BOX}>
                <p>Em uma descri&ccedil;&atilde;o simples, cooperar &eacute; trabalhar junto para atingir objetivos compartilhados. Em atividades cooperativas indiv&iacute;duos buscam resultados que sejam ben&eacute;ficos para eles pr&oacute;prios bem como para os demais membros do grupo. (Johnson e Johnson, 2017).</p>
            </Box>
        </CenterBoxContainer>
    </div>
)

export default Conceituacao